/* 100vh para indicar use todo el 100% de altura del dispositivo 
   todo lo que este relacionado con div cuya className = "App" */
.App {
  font-family: 'Oxygen', sans-serif;
  height: 100vh;
  background: rgb(238, 69, 64);
  background: linear-gradient(
    180deg,
    rgba(238, 69, 64, 1) 0%,
    rgba(199, 44, 65, 1) 15%,
    rgba(128, 19, 54, 1) 30%,
    rgba(81, 10, 50, 1) 45%,
    rgba(45, 20, 44, 1) 100%
  );
}
