/* efecto sombra al hacer hover sobre la tarjeta, parametros tamaño hacia cada lado NSEO y en el rgba
   colores claros y opacidad 0.273 agregar !important para que BS4 no lo sobreescriba */
.card:hover {
  box-shadow: 5px 10px 20px 1px rgba(255, 255, 255, 0.273) !important;
  transition: all 0.7s linear; /* transicion desde todos los lados y a los 0.7 seg y lineal */
}

.card-body {
  padding: 2rem 0 !important;
}

.card-text {
  padding: 0.3rem 1rem;
  font-size: 1rem;
}

/* esta clase se crea para evitar que la imagen se desborde al aplicar transform: scale(1.8);
   ocultan lo que ocurre el div que contiene la etiqueta img */
.overflow {
  overflow: hidden;
}

/* para que no sea de golpe agregamos un efecto de transicion  en la imagen etiqueta img */
.card-img-top {
  transform: scale(1);
  transition: all 0.2s ease-in;
}

/* efecto de agrandar imagen al pasar mouse sobre la imagen de la etiqueta img */
.card-img-top:hover {
  transform: scale(1.8);
}
